// dark theme base
$richBlack: rgba(0, 15, 8, 1);
$red: #7D141E;
$white: #fff;
$blackOlive: rgba(58, 62, 59, 1);
$slateGrey: rgba(112, 121, 140, 1);
$isabelline: rgba(245, 241, 237, 1);

// dark theme lighten
$richBlackLight: lighten($richBlack, 30%);
$redLight: lighten($red, 30%);
$blackOliveLight: lighten($blackOlive, 30%);
$slateGreyLight: lighten($slateGrey, 30%);

// dark theme darken
$redDark: darken($red, 15%);
$blackOliveDark: darken($blackOlive, 25%);
$slateGreyDark: darken($slateGrey, 30%);
$isabellineDark: darken($isabelline, 30%);

// extra colors
$lightblue: #7395ae;
$darkblue: #557a95;
$wheat: #b1a296;

